.c-btn{
	text-transform: uppercase;
	font-size: 0.8125rem;
	border-radius: 5px;
	height: 27px;
	padding: 0 10px;
	color: #fff;
	display: flex;
	align-items: center;
	justify-content: center;
	@media (max-width:$bp-sm-2) {
		font-size: 0.6875rem;
	}
	&_lg{
		height: 48px;
		width: 100%;
		max-height: 270px;
		font-size: 1.0625rem;
		font-weight: bold;
	}
	&_bordered{
		box-shadow: inset 0 0 0 3px #f1ca49;
	}
	&_secondary{
		background-color: #f1ca49;
		&:hover{
			color: #fff;
		}
	}
	
	&_danger{
		background: #dc3545;
	}

	&_primary{
		&.is-active{
			background-color: #f1ca49;
		}
		background-color: #8cafc2;
		&:hover{
			color: #fff;
			background-color: #f1ca49;
		}
	}
	&_blue{
		background-color: #3e7ce3;
		height: 40px;
		display: inline-flex;
		padding: 0 25px;
		&:hover{
			color: #fff;
		}
	}
}